//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $family: null) {
	@if $family != null {
		font-family: $family; }
	@else {
		font-family: 'Lato'; }
	font-style: $style;
	font-weight: $weight; }
// Ubuntu
//light
@font-face {
	@include fontdefault(normal, 300);
	@include fontpath('Ubuntu-Light', 'Ubuntu-Light', lato-light); }
@font-face {
	@include fontdefault(italic, 300);
	@include fontpath('Ubuntu-LightItalic', 'Ubuntu-LightItalic', lato-lightitalic); }
//regular
@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('Ubuntu', 'Ubuntu', lato-regular); }
@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('Ubuntu-Italic', 'Ubuntu-Italic', lato-italic); }
//medium
@font-face {
	@include fontdefault(normal, 500);
	@include fontpath('Ubuntu-Medium', 'Ubuntu-Medium', lato-medium); }
@font-face {
	@include fontdefault(italic, 500);
	@include fontpath('Ubuntu-MediumItalic', 'Ubuntu-MediumItalic', lato-mediumitalic); }
//bold
@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('Ubuntu-Bold', 'Ubuntu-Bold', lato-bold); }
@font-face {
	@include fontdefault(italic, 700);
	@include fontpath('Ubuntu-BoldItalic', 'Ubuntu-BoldItalic', lato-bolditalic); }

